
import {Component, Prop, Vue} from "vue-property-decorator";

@Component({ name: "car-sprite" })
export default class CarSprite extends Vue {
  @Prop({ type: Number, default: 0 })
  x!: number;

  @Prop({ type: Number, default: 0 })
  y!: number;

  @Prop({ type: Number, default: 0 })
  speed!: number;

  @Prop({ type: Number, default: 0 })
  maxWidth!: number;

  @Prop({ type: String, required: true })
  sprite!: number;

  myX = this.x;

  mainInterval(): void {
    setInterval(() => {
      this.myX += this.speed;
      if (this.myX > this.maxWidth) {
        this.myX = -20;
      }
    }, 10);
  }

  created(): void {
    this.mainInterval();
  }
}
