
import { Component, Vue } from "vue-property-decorator";
import axios from "axios";
import WhatYouDoing from "@/components/WhatYouDoing.vue";
import Loading from "@/components/Loading.vue";

@Component({ name: "main-forza",
  components: {Loading, WhatYouDoing}
})
export default class MainForza extends Vue {
  actualTime = 0;
  days = 0;
  hours = 0;
  minutes = 0;
  seconds = 0;

  loading = true;
  onWaiting = true;

  countDown(): void {
    setInterval(() => {
      if (!this.loading && this.onWaiting) {
        const timeLeft: number = new Date("Nov 9, 2021 00:00:00").getTime();
        const diff = timeLeft - this.actualTime;
        if (diff > 0) {
          this.onWaiting = true;

          this.days = Math.floor(diff / (1000 * 60 * 60 * 24));
          this.hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
          this.minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
          this.seconds = Math.floor((diff % (1000 * 60)) / 1000);
        } else {
          this.onWaiting = false;
        }

        this.actualTime = this.actualTime + 1000;
      }
    }, 1000);
  }

  async created(): Promise<void> {
    const result = await axios.get('https://worldtimeapi.org/api/ip');

    if (!isNaN(result.data.unixtime)) {
      this.actualTime = new Date(result.data.datetime).getTime();
      this.loading = false;
    }
  }

  mounted(): void {
    this.countDown();
  }
}
