
import { Component, Vue } from "vue-property-decorator";
import CarSprite from "@/components/CarSprite.vue";

type Car = {
  x: number;
  y: number;
  speed: number;
  asset: string;
}

@Component({ name: "what-you-doing",
  components: {CarSprite}
})
export default class WhatYouDoing extends Vue {
  totalCars = 5;
  cars: Car[] = [];
  carsSprites = [
    require("../assets/car-1.svg"),
    require("../assets/car-2.svg"),
    require("../assets/car-3.svg"),
    require("../assets/car-4.svg"),
  ];
  screenWidth = 0;
  screenHeight = 0;

  created(): void {
    this.screenHeight = window.innerHeight;
    this.screenWidth = window.innerWidth;

    window.addEventListener("resize", () =>{
      this.screenHeight = window.innerHeight;
      this.screenWidth = window.innerWidth;
    });

    for(let i = 0; i < this.totalCars; i++) {
      this.cars.push({
        x: Math.floor(Math.random() * (-20 - (-200))) + (-1),
        y: Math.floor(Math.random() * (this.screenHeight)),
        speed: Math.floor(Math.random() * (5 - 0.3)) + 0.3,
        asset: this.carsSprites[Math.floor(Math.random() * (4 - 1)) + 1]
      })
    }
  }
}
